body {
  margin: 0;
}

:root {
  --colorlink: #000;
  --colorhover: #00acdc;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header--logolink {
  margin-left: 50px;
  display: flex;
}

.header--navigation {
  margin-right: 50px;
}

ul {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_list--link {
  text-align: center;
  text-transform: uppercase;
  color: var(--colorlink);
  align-items: center;
  height: 60px;
  margin: 0 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav_list--link:hover {
  color: var(--colorhover);
}

.is-active {
  color: var(--colorhover);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--colorhover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.margin-left {
  margin: 0 10px 0 0;
}

.margin-right {
  margin: 0 0 0 10px;
}
